import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Element_maker.css';
import { Form,Button,Message} from 'semantic-ui-react'
import{handleData} from"./handlingdata";
import eNum from "./inmo"  
import Select from 'react-select';
import EleTable from "./EleTable"



class Element_maker extends React.Component {
 
    constructor(props) {
      super(props);
      this.state = {
        number:"",
        name:"",
        command:"",
        tag:"input",
        kind:"number",
        values:[],
        default_value:"",
        order:"",
        optionVal:"",
        optionName:"",
        tableData:this.props.elementData

        }
    
        console.log(props)
    }
  
     handleSubmit = (event)=> {
       console.log(this.state)
      event.preventDefault();
      handleData(this.state,()=> {
       fetch('https://hereoserverel.herokuapp.com/api')
       .then((res) => res.json())
       .then((data)=>{
       this.setState({
tableData:data

       })

       })
        

      })

     
    }
    
    change = e=> {
      this.setState({[e.target.name]: e.target.value });
      
    }

    handelSelect=()=>{
   
      if(this.state.tag=="select"||this.state.tag=="checkbox"){
       return(
         <div>
        <Form.Input value={this.state.optionVal} label="add option values separated by commas" placeholder='example: 10, 20, 30'  name="optionVal" onChange={e=>this.change(e)}/>
        <Form.Input value={this.state.optionName} label="add option names separated by commas" placeholder='example: low, mid, high'  name="optionName" onChange={e=>this.change(e)}/>
        </div>
         )
      }
    }

    
    logChange=(val)=> {
      const select_val=val.value;
      this.setState({
     number:select_val
     
      })
     }


    del=(id)=>{
      const url = "https://hereoserverel.herokuapp.com/ele"
      fetch(url + '/' + id, {
        method: 'delete'
      }).then(response =>
        response.json().then(json => {
          fetch('https://hereoserverel.herokuapp.com/api')
          .then((res) => res.json())
          .then((data)=>{
          this.setState({
   tableData:data
   
          })
   
          })

          return json;
        })
      );

      
    }
  
      
      render() {
        var options = eNum.propertys


        const{number, name,tag, kind, default_value,order,command}=this.state
      
     
            
          return (
            <div className={s.root}>
            <div className={s.container}>
    <div className={s.form}>
    <Form onSubmit={this.handleSubmit} className={s.flexForm} >
    <Select
            name="form-field-name"
            value={number}
            options={options}
            onChange={this.logChange}
          />
         
          <Form.Group widths='equal' >
            <Form.Input value={name}  label='name' placeholder='name'  name="text" name="name"  onChange={e=>this.change(e)}/>
            <Form.Input value={command}  label='command' placeholder='example:hereo_step_leash'  name="text" name="command"  onChange={e=>this.change(e)}/>
            <Form.Input value={default_value} label='default value' placeholder='default value' name="default_value"  onChange={e=>this.change(e)}/>
            <Form.Input value={order}  label='order*' placeholder='order' type='number' name="order"  onChange={e=>this.change(e)} />
            </Form.Group>
            <Form.Group widths='equal'>
            <select value={tag} label='tag' label='tag' name="tag"  onChange={e=>this.change(e)}>
            <option value="input">Input</option>
            <option value="select">Select</option>
            <option value="checkbox">Checkbox</option>            
            </select>
            <select value={kind} label='kind' placeholder='kind' name="kind"  onChange={e=>this.change(e)}>
            <option value="number">Number</option>
            <option value="text">Text</option>
            <option value="email">Email</option>            
            </select>
          </Form.Group>
          <Form.Group widths='equal' >
            {this.handelSelect()}
            </Form.Group>
            <Form.Group widths='equal' >
          <Button type='submit'>Save</Button>
          </Form.Group>
          </Form>

          <EleTable number={number} data={this.state.tableData} del={this.del}/>


    
          </div>
      
      
            </div> 
  
            </div>  
            
          )
      }
  }
  

  

export default withStyles(s)(Element_maker);



