export function handleData(data,callback){
    console.log(data)
    const{number, name,tag, kind, default_value,order,command}=data
let element={
name,
command,
number,
order,
default_value,
html:{
tag:tag,
kind:kind
},



}

let arrayName = data.optionName.split(",")
let arrayVal = data.optionVal.split(",")
element.values=[]


for(var i=0;i<arrayName.length;i++){
element.values.push({ 
name:arrayName[i],
val:arrayVal[i]

})

}

console.log(JSON.stringify(element))

fetch("https://hereoserverel.herokuapp.com/api",{
    method: 'post',
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    }, 
    body:JSON.stringify(element)

}).then(()=>{
    callback()


    
})
  



console.log()

// var qSchem = new  mongoose.Schema({
//     html: {tag: String, kind: String},
//         number: {type: Number},
//         default_value: Number,
//         order: Number,
//         values:Array
    
        
    
//     })
    


}


