import React from 'react';
import Layout from '../../components/Layout';
import Element_maker from './Element_maker';


async function action({fetch}) {
  const resp = await fetch('https://hereoserverel.herokuapp.com/api', {
    
    });
    const data = await resp.json();
    console.log(data)
    if (!data) throw new Error('Failed to load the data');
  return {
    chunks: ['home'],
   
    component: (
      <Layout>
        <Element_maker elementData={data}/>
      </Layout>
    ),
  };
}

export default action;



