import React from 'react';
import s from './Element_maker.css';
import withStyles from 'isomorphic-style-loader/lib/withStyles';

import { Table,Form,Button,Message} from 'semantic-ui-react'



class EleTable extends React.Component {
    
       constructor(props) {
         super(props);
    
       }
       handleDel=(e)=>{
    console.log(e.target.value)
     this.props.del(e.target.value)

       }

         
         render() {
            
        let {number,data} = this.props

data =data.filter((item)=>{
    return item.number==number

   

})
console.log(data)

             return (
           <div className={s.eleTable}>
           
    <Table singleLine>
    <Table.Header>
       <Table.Row>
       <Table.HeaderCell>Name</Table.HeaderCell>
       <Table.HeaderCell>Command</Table.HeaderCell>
       <Table.HeaderCell>Default Value</Table.HeaderCell>
       <Table.HeaderCell>Tag</Table.HeaderCell>
       <Table.HeaderCell>Kind</Table.HeaderCell>
       <Table.HeaderCell>Order</Table.HeaderCell>
       <Table.HeaderCell>Delete</Table.HeaderCell>
       </Table.Row>
    </Table.Header>
    <Table.Body>
    {data.map((data, key) => {
       return (
       <Table.Row key={key}>
         <Table.Cell>{data.name}</Table.Cell>
         <Table.Cell>{data.command}</Table.Cell>
       <Table.Cell>{data.default_value}</Table.Cell>
       <Table.Cell>{data.html.tag}</Table.Cell>
       <Table.Cell>{data.html.kind}</Table.Cell>
       <Table.Cell>{data.order}</Table.Cell>
     
       <Table.Cell><Button value={data._id} onClick={this.handleDel}  size='mini' color='red'>Delete</Button></Table.Cell>
       </Table.Row>
       )
    })}
    </Table.Body>
  </Table>
  
  </div>
     

           
           
           
    
               




         
             )
         }
     }
     
   
     
   
   export default withStyles(s)(EleTable);
   


